:root{
  --primary-color: #9b26af; 
  --secondary-color: rgb(224, 194, 253); 
  --primary-font-color:#26002d;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{
  height: 100vh;
  background-image: linear-gradient(179deg, #03a9f4 20%,#ffc200);
}



@media screen and (max-width:800px) {
  html{
      font-size: 12px;
  }
}
  


.MainHeading{
    padding: 0.5rem;
    display: flex;
    justify-content: center;
    background-color: var(--secondary-color);
}

.MainHeading h2{
    color:var(--primary-color);
}

.Buttons{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    flex: 1 1;
    padding: 1rem;
    background-color: var(--primary-color);
}

.prev-next-btn{
    flex: 1 1;
    display: flex;
    justify-content: flex-end;
    grid-gap: 1rem;
    gap: 1rem;
}


button.prev-question , button.next-question , button.answer-check{
    min-width: 5rem;
    border: 2px double purple;
    font-weight: 600;
    color: var(--primary-color);
    background-color: var(--secondary-color);
    transition: background-color  0.5s ease, color  0.5s ease;

}

button.prev-question:hover , button.next-question:hover , button.answer-check:hover{
    background-color: var(--primary-color);
    color: var(--secondary-color);
    border: 2px double var(--secondary-color);
}    
.OptionsContainer{
    padding: 1rem 1rem 0.5rem 1rem;
    background-color: var(--secondary-color);
    flex: 1 1;
}

.option{
    background-color: var(--primary-color);
    padding: 0.5rem;
    margin-bottom: 0.5rem;
    transition: opacity 0.5s ease;
    display: grid;
    grid-template-columns: 1fr -webkit-max-content;
    grid-template-columns: 1fr max-content;
    border-radius: 4px;
}

.option:hover{
    opacity: 0.7;
}

input[type = "radio"]{
    width: 1rem;
}


.green-option{
    background-color: green;
}

.red-option{
    background-color: red;
}
.Question{
    padding: 1rem;
    background-color: var(--primary-color);
    font-weight: 600;
    color: var(--primary-font-color);
}
.QuestionArea{
    flex: 5 1;
    box-shadow: 0px 0px 3px 1px rgb(210, 210, 210);
    border-radius: 1rem;
    overflow: auto;
    display: flex;
    flex-direction: column;
}
.ResultArea{
    flex: 2 1;
    box-shadow: 0px 0px 3px 1px rgb(210, 210, 210);
    border-radius: 1rem;
    overflow: auto;
    display: flex;
    flex-direction: column;
}

.heading-result-area{
    padding: 1rem;
    background-color: var(--primary-color);
    font-weight: 600;
    color: var(--primary-font-color);
}

.item-result-area{
    padding: 1rem;
    background-color: var(--secondary-color);
    flex: 1 1;
}

.item-result-area div{
    background-color: var(--primary-color);
    margin-bottom: 0.5rem;
    display: grid;
    grid-template-columns: 1fr 1rem;
    padding: 0.5rem;
    border-radius: 4px;
}


.val{
    display: flex;
    justify-content: center;
    align-items: center;
}


.MainBody{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    padding: 1rem;
    max-width: 1000px;
    margin:  auto;

}


@media screen and (max-width:800px) {
    .MainBody{
        flex-direction: column;
    }
}
    
.NavBar{
    padding: 0 1rem;
    display: flex;
    border-bottom: 2px solid grey;
}

.logo-container{
    width: 7rem;
    padding-top: 5px;
}

.logo-container img{
    max-width: 100%;
}

.nav-other-buttons{
    flex: 1 1;
    display: flex;
    justify-content: flex-end;
}

.nav-other-buttons button{
    min-width: 5rem;
    border: none;
    background:none;
    font-weight: 700;
    color: rgb(50, 50, 50);
    cursor: pointer;
}

.nav-other-buttons button:hover{
    background-color: rgb(186, 186, 253);
}
